<script>
import {computed, inject, onMounted, ref} from 'vue'
import {getCurrentInstance} from 'vue'
import root_content from "@/components/rootcontent.vue";
import {useStore} from "vuex";

export default {
  name: 'banner_h',
  components: {
    root_content,
  },
  setup() {
    const store = useStore()
    let w = computed(()=>store.state.w)
    let piclayout = inject('piclayout')
    const n = ref(0)
    const instance = getCurrentInstance()
    onMounted(() => {
      piclayout([1,1,1,1,0],[1,0,1,1,1])
      autoscroll()
    })
    function autoscroll() {
      const el = instance.refs.banner
      el.style.marginLeft = n.value + 'px'
      setInterval(() => {
        el.style.marginLeft = n.value + 'px'
        n.value -= 1000
        if (n.value < -3000) {
          n.value = 0
        }
      }, 2500)
    }
    function changescroll(value) {
      const el = instance.refs.banner
      switch (value) {
        case 1:
          el.style.marginLeft = 0;
          break;
        case 2:
          el.style.marginLeft = -1000 + 'px';
          break;
        case 3:
          el.style.marginLeft = -2000 + 'px';
          break;
        case 4:
          el.style.marginLeft = -3000 + 'px';
          break;
        case 6:
          n.value += 1000;
          if (n.value > 0) n.value = -3000;
          el.style.marginLeft = n.value + 'px';
          console.log(n.value);
          break;
        case 5:
          n.value -= 1000;
          if (n.value < -3000) n.value = 0;
          el.style.marginLeft = n.value + 'px';
          console.log(n.value);
          break;
        default:
          break;
      }
    }
    let bannerslim = computed(()=>{
      if (w.value){return {'banner-slim': true}}
      else {return {'banner-slim': false,}}
    })
    return {
      n, w,
      changescroll, bannerslim
    }
  },
}
</script>

<template>

  <div class="banner" :class="bannerslim">

    <img class="b-img" src="../assets/mypic/banner.png" ref="banner" alt="" usemap="#map">
    <map name="map" id="map">
      <router-link to="/seasons">
        <area style="cursor: pointer" shape="rect" coords="0,0,1000,560" alt="">
      </router-link>
      <router-link to="/news/1">
        <area style="cursor: pointer" shape="rect" coords="1001,0,2000,560" alt="">
      </router-link>
      <router-link to="/news/1">
        <area style="cursor: pointer" shape="rect" coords="2001,0,3000,560" alt="">
      </router-link>
      <router-link to="/picset">
        <area style="cursor: pointer" shape="rect" coords="3001,0,4000,560" alt="">
      </router-link>
    </map>

    <div class="dot dot1" @click="changescroll(1)"></div>
    <div class="dot dot2" @click="changescroll(2)"></div>
    <div class="dot dot3" @click="changescroll(3)"></div>
    <div class="dot dot4" @click="changescroll(4)"></div>
    <div class="b-btn b-btn1" @click="changescroll(5)">》</div>
    <div class="b-btn b-btn2" @click="changescroll(6)">《</div>

    <div v-show="w" class="slim-banner">
      <router-link to="/seasons"><img class="s-img" src="../assets/slim/JOJOASBR.png" alt=""></router-link>
      <router-link to="/news/1"><img class="s-img" src="../assets/slim/BANNER2.png" alt=""></router-link>
      <router-link to="/news/1"><img class="s-img" src="../assets/slim/Stable_Diffusion.png" alt=""></router-link>
      <router-link to="/picset"><img class="s-img" src="../assets/slim/banner4.png" alt=""></router-link>
    </div>

  </div>

  <root_content/>

</template>

<style scoped>
.banner {
  width: 1000px;
  height: 563px;
  margin: 0 auto;
  display: flex;
  overflow: hidden;
//cursor: pointer;
}

.b-img {
  transition: all;
  transition-duration: 0.8s;
  margin-right: 0;
}

@keyframes lbt {
  0% {
    transform: translateX(0);
    transition-duration: 1s;
    transition-timing-function: ease-in;
  }
  25% {
    transform: translateX(-1000px);
    transition-duration: 1s;
    transition-timing-function: ease-in;
  }
  50% {
    transform: translateX(-2000px);
    transition-duration: 1s;
    transition-timing-function: ease-in;
  }
  75% {
    transform: translateX(-3000px);
    transition-duration: 1s;
    transition-timing-function: ease-in;
  }
  100% {
    transform: translateX(0);
    transition-duration: 1s;
    transition-timing-function: ease-in;
  }
}

.dot {
  background-color: whitesmoke;
  width: 12px;
  height: 12px;
  border: 1px solid white;
  position: absolute;
  border-radius: 6px;
  margin-left: 470px;
  margin-top: 500px;
  opacity: 0.7;
  cursor: auto;
}

.dot2 {
  margin-left: 490px;
}

.dot3 {
  margin-left: 510px;
}

.dot4 {
  margin-left: 530px;
}

.b-btn {
  width: 40px;
  height: 80px;
  position: absolute;
  color: whitesmoke;
  margin-top: 251px;
  font-size: 40px;
  line-height: 80px;
}

.b-btn:hover {
  background-color: white;
  opacity: 0.6;
  cursor: pointer;
}

.b-btn2 {
  margin-left: 0;
}

.b-btn1 {
  margin-left: 960px;
}

@media screen and (min-width: 320px) and (max-width: 700px) {
  .dot, .b-btn, .b-img, #map {
    display: none;
  }

  .banner-slim {
    width: 100%;
    height: auto;
  }
  .s-img{
    width: 100%;
    cursor: pointer;
  }
}
</style>