    const chinfo = {"status": 1, "characters": [{"id": 1, "image": "characters/乔纳森乔斯达.png", "name": "乔纳森·乔斯达", "status": "1889年1月7日去世", "side": "真正的贵族 一代jojo", "detail": "透过呼吸与体内的血液循环，激发出生命能量的技术，师承齐贝林男爵。\r\n乔纳森的人设原型是著名美国导演史蒂文·斯皮尔伯格。\r\n成为绅士可以说是大乔从小到大的目标和理想萌点，贯穿了他的一生，也作为他最大的闪光点被大家所铭记。", "season_id": 1, "stand": 1}, {"id": 2, "image": "characters/Robert_E._O._Speedwagon.png", "name": "罗伯特·E·O·史比特瓦根", "status": "心脏病死亡且终身未娶", "side": "初代jojo的挚友", "detail": "史比特瓦根在《JOJO的奇妙冒险》第一部出场，影响贯穿前两部，而他创建的史比特瓦根基金会（SPW基金会）更是影响了整个旧世界专业给乔斯达家擦屁股。\r\n\r\n初登场时是食尸鬼街的混混头目，后来被乔纳森的绅士精神打动，从而成为乔纳森的左膀右臂（解说员）。\r\n\r\n自称走遍了全世界看过各种事物，见过美洲的稀有动物、亚洲的奇怪植物、加勒比海的大树、还有巨大的龙卷风，阅历极其丰富，但对吸血鬼、柱之男这类超自然生物却一无所知。\r\n\r\n虽然住在贫民街但其实相当富裕，拥有一辆四人座煤炭动力蒸汽汽车。此外也十分有商业头脑，为他之后创建SPW基金会打下了基础。\r\n\r\n乔纳森死后，史比特瓦根担负起照看乔斯达家的责任，并且在美国西部发现油田，成为石油大亨并创建了SPW基金会，最终在89岁高龄时突发心脏病逝世。\r\n\r\n本文引自萌娘百科(https://zh.moegirl.org.cn )", "season_id": 1, "stand": 1}, {"id": 3, "image": "characters/威廉A齐贝林.png", "name": "威廉·A·齐贝林", "status": "被塔拉斯腰斩后身亡", "side": "JoJo一代的师傅", "detail": "人类的赞歌是勇气的赞歌！人类的伟大就是勇气的伟大！\r\n齐贝林是一位功力深厚的波纹斗士，毕生致力于寻找并消灭石鬼面。\r\n\r\n作为JOJO系列最初的“JOJO的亲密同伴”系角色之一，齐贝林大叔以其搞怪中带严肃的风格在JOJO粉心中留下了深刻的印象", "season_id": 1, "stand": 1}, {"id": 4, "image": "characters/艾莉娜班德鲁顿.png", "name": "艾莉娜·班德鲁顿", "status": "英国乡下出生享年81岁", "side": "乔纳森·乔斯达的妻子", "detail": "艾莉娜出生在英国乡下，小时候受人欺负被乔纳森所救，虽然乔纳森被打得灰头土脸，但是艾莉娜却也因此对乔纳森有了好感，在乔纳森因迪奥的缘故被其他同龄人孤立时成为了乔纳森的精神支柱。但在那之后，DIO为了摧毁乔纳森的精神支柱而强吻了艾莉娜。\r\n\r\n在被DIO强吻后不久，艾莉娜和家人出国前往印度，和大乔断了联系。\r\n\r\n在DIO变成吸血鬼后，乔纳森和DIO经过死战，DIO暂时消失，但乔纳森也因此受伤，此时成为护士的艾莉娜得以和乔纳森重逢。二人在那之后遇到了齐贝林男爵，乔纳森跟随齐贝林男爵学习波纹气功，而艾莉娜则等待其打败DIO归来。\r\n\r\n在乔纳森打败DIO后两人结婚，但在蜜月时，仅剩下一个头的DIO袭击了乔纳森所在的轮船，在轮船爆炸前乔纳森让艾莉娜带着刚出生不久的女婴离开，之后艾莉娜在海上漂流数日得以获救，并在那之后生下了乔治·乔斯达二世。", "season_id": 1, "stand": 1}, {"id": 5, "image": "characters/多配地.png", "name": "多配地", "status": "死亡", "side": "高僧 波纹战士", "detail": "西藏的波纹大师，曾收过威廉·A·齐贝林为徒。在威廉·A·齐贝林修行满三年时曾劝告过他一些东西——在一个充满尸臭味的古老密室，幼童将门打开之时，为了解放被禁锢的年轻雄狮让其迈向未来，你将献上自己的生命。但是威廉·A·齐贝林却接受了命运。之后多配地和自己的徒弟旦亚、史特雷一起帮助乔纳森·乔斯达一起击败了迪奥·布兰度，再之后和大家一起为新婚的乔纳森夫妇二人送行，其后事迹不详。。", "season_id": 1, "stand": 1}, {"id": 6, "image": "characters/旦亚.png", "name": "旦亚", "status": "英勇战死", "side": "齐贝林的波纹师兄弟", "detail": "跟随多佩地前来协助乔纳森的波纹战士之一。\r\n是和齐贝林一同修练波纹的师兄弟，两人交情似乎相当深厚。\r\n与史特雷的名字都出自英国摇滚乐队Dire Straits。\r\n为了替死去的齐贝林复仇而挑战迪奥，不过却败在迪奥的气化冷冻法之下，全身被冰冻击碎，头部落入蔷薇中，但在死前吐出灌入波纹的蔷薇刺瞎了迪奥的右眼。", "season_id": 1, "stand": 1}, {"id": 7, "image": "characters/史特雷.png", "name": "史特雷", "status": "被乔瑟夫击败后发动波纹自尽", "side": "波纹战士", "detail": "第一部事件结束后，多配地可能隐退或者逝世，史特雷接替他成为了波纹使者的领袖，并与史比特瓦根互相熟识。\r\n迪奥被击败50年后，史比特瓦根和史特雷前往墨西哥，因为羡慕迪奥的强大、不老、美丽，史特雷难耐冲动杀死了在场的其他波纹使者和史比特瓦根基金会成员，重伤了史比特瓦根，并用他的血激活石鬼面变成吸血鬼。\r\n被JOJO打败后史特雷感叹乔瑟夫继承了乔纳森的精神后，把柱之男的事情告诉了乔瑟夫，随后在体内运起波纹自杀。", "season_id": 2, "stand": 1}, {"id": 8, "image": "characters/迪奥布兰度.png", "name": "迪奥·布兰度", "status": "人-吸血鬼-神？", "side": "究极邪恶", "detail": "名字DIO来自于重金属乐队Dio[14]，同时这个名字也是意大利语中“神”的意思。所以第五部是个神之子Gio日天驱魔的故事。姓氏“布兰度”来自著名演员“马龙·布兰度/白兰度”\r\n\r\n虽然戏份只在第一部和第三部，但影响延续到第六部，是整个系列漫画的核心人物之一。乃至后期所有人都忘了迪奥最初的目的是想要抢乔斯达家族的家产\r\n\r\nDIO性格残忍无情，但其却具备足以让全世界的恶人都拜服于他的独特人格魅力，被称之为“恶人的救世主”。\r\n\r\n其外貌在设定书中被形容为“妖异美丽、容姿端丽”，原作漫画中多用“美丽”“性感”等词形容其外貌，曾被形容为有着不似男人的妖媚，言语透着危险的甘美。\r\n\r\n第二部漫画中史特雷（设定上也是美男子）在第一部决战后对DIO的容貌、力量记忆并向往了50年之久（算是史特雷堕落的间接原因）。\r\n\r\n第三部中被jojo完全打死", "season_id": 1, "stand": 2}, {"id": 9, "image": "characters/温青.png", "name": "温青", "status": "死亡", "side": "迪奥部下", "detail": "第一部幻影之血的小角色，大乔的对手，dio的部下。曾在食尸鬼街为迪奥提供毒药。\r\n在大乔结婚度蜜月的时候和迪奥偷袭大乔。\r\n会点算命？", "season_id": 1, "stand": 1}, {"id": 10, "image": "characters/开膛手杰克.png", "name": "开膛手杰克", "status": "死亡", "side": "被迪奥复活后再次死亡", "detail": "著名的杀人犯，是历史资料里存在的人物，其身份与下落都无人知晓。 被迪奥变成尸生人（Zombie）来为他做事。", "season_id": 1, "stand": 1}, {"id": 11, "image": "characters/塔卡斯.png", "name": "塔卡斯", "status": "被迪奥复活后再次死亡", "side": "迪奥复活为尸生人的英国骑士", "detail": "体型庞大，力大无穷。\r\n\r\n十分擅长死亡锁链竞技场内的战斗，必杀技为天地来蛇杀，自身与锁链从上下两个方位夹击敌人并将敌人缠住，随后使出浑身解数，用铁链将敌人的身体绞断。\r\n\r\n着实比不上重视骑士精神的布拉霍，变成尸生人后因为生前的怨恨和不甘而忘记身为骑士的荣耀，成了以杀戮为乐的怪物。\r\n\r\n名字出自英国前卫摇滚乐团 Emerson, Lake & Palmer (ELP) 的第二张专辑Tarkus。", "season_id": 1, "stand": 1}, {"id": 12, "image": "characters/布拉霍.png", "name": "布拉霍", "status": "被迪奥复活后死亡", "side": "被迪奥复活为尸生人的双骑士之一", "detail": "佩剑为女王所赠的宝剑，剑法超群，以头发作为武器，能用头发来吸血或是持剑作战，绝招是“死发舞剑”。\r\n\r\n具有骑士精神，并不像塔卡斯一样完全丧失本性。被乔纳森的黄金精神所感染，逐渐从生前的怨恨中解脱。\r\n\r\n名字出自英国鼓手Bill Bruford。", "season_id": 1, "stand": 1}, {"id": 13, "image": "characters/怪人杜比.png", "name": "怪人杜比", "status": "死亡", "side": "迪奥创造出的融合体尸生人", "detail": "迪奥创造出的融合体尸生人，处于风骑士之镇的城堡中，头里养着几条剧毒的毒蛇，身上有很多不明液体。平时用袋子包裹住整个头部，以隐藏头上的毒蛇和自己的骇人的脸。和其他尸生人一样有强大的力量，可以在墙壁上攀爬。\r\n\r\n名字源于美国乐队Doobie Brothers。", "season_id": 1, "stand": 1}, {"id": 14, "image": "characters/乔治乔斯达一世.png", "name": "乔治·乔斯达一世", "status": "被养子迪奥害死", "side": "善良的英国贵族", "detail": "英国贵族，乔纳森的父亲。平常不时会外出去进行贸易。\r\n\r\n初登场时是黑发，七年后便成了白发。\r\n\r\n性格过于绅士、善良，从不会怀疑人或以恶意揣测他人。\r\n\r\n在教导乔纳森时，会表现出十分严厉的一面，收养迪奥后开始更加苛刻地对待乔纳森，但本意是希望儿子能变得优秀成熟\r\n\r\n一切都是出于一个父亲对孩子的爱。\r\n\r\n乔纳森上大学时，乔治被迪奥下毒，很长的时间都卧病在床。在乔纳森揭发迪奥后，乔治带着警察阻去了迪奥的退路，使迪奥被逼入绝境。不过迪奥却突然取出小刀，打算刺杀乔纳森，乔治就在这时冲瞬移出来替乔纳森挡下了那一刀并受了致命伤，死前希望乔纳森别怨恨迪奥。", "season_id": 1, "stand": 1}, {"id": 15, "image": "characters/玛丽乔斯达.png", "name": "玛丽·乔斯达", "status": "车祸死亡", "side": "大乔母亲", "detail": "在1868年的某一天，由于下着大雨，使得其乘坐的马车滚落悬崖自己为拯救儿子乔纳森而死。\r\n\r\n在迪奥来到乔家之后，由于处处都超越着乔纳森，在乔纳森的饭碗被父亲剥夺后跑回房间看着母亲的照片难过然而紧接着发现了一袋巧克力，立刻就狼吞虎咽起来。\r\n\r\n在TV中，乔纳森成年后研究石鬼面时，正是把母亲的照片摆在桌上。", "season_id": 1, "stand": 1}, {"id": 16, "image": "characters/达利欧布兰度.png", "name": "达利欧·布兰度", "status": "被迪奥毒死", "side": "迪奥父亲社会渣滓", "detail": "迪奥·布兰度的生父，但却是个不折不扣的混混，也就是因为他，迪奥·布兰度也变得越来越邪恶。\r\n生活在贫民街，常年酗酒、性格恶质，是个彻头彻尾的人渣，就连迪奥都对他恨之入骨。\r\n整日酗酒的他因身体虚弱只能醉醺醺的躺在床上。而因此完全抹灭了尽孝的心意的迪奥用东洋毒药加速了父亲的死亡。\r\n迪奥曾说过母亲是因为父亲的关系才会过度劳累而死，并且在剧情中也为了母亲的事而发怒，由此可见迪奥小时候对母亲有相当重要的感情，当达利欧要求迪奥卖掉母亲的遗物时，迪奥愤怒的流下了眼泪。\r\n1880年，达利欧临终前因为放心不下迪奥，加着想起自己和乔斯达伯爵的一面之缘，特地写了一封信请求迪奥交给乔斯达伯爵，好让迪奥接受乔斯达伯爵的照顾令生活无后顾之忧，要求迪奥「务必要成为不输任何人的有钱人」后便逝世。", "season_id": 1, "stand": 1}, {"id": 17, "image": "characters/乔瑟夫乔斯达.png", "name": "乔瑟夫·乔斯达", "status": "健在相当长寿", "side": "二代JOJO", "detail": "和没练过波纹的父亲不同，乔瑟夫天生就会使用波纹。\r\n童年时期父亲早逝，遂交奶奶艾莉娜抚养。\r\n乔瑟夫的一生经历了什么样的灾难都能够成功存活下来。仅仅在月下三兄贵手中，乔瑟夫运用本身的战斗力、勇气、运气、谋略与智慧还能成功胜利，是所有JOJO中能力最多样化的一个（综合实力（狗屎运）最强的jojo），整个JOJO系列里最强的敌人，成为究极生物的卡兹也被他打败，甚至被火山爆发冲出地球后还能平安无事的回来。\r\n虽然第三部的乔瑟夫由于年迈加上不是主角导致智斗成分大大削弱，替身也是主角团中最没有攻击性的一个，但依旧无法掩盖二乔丰富的战斗经验和灵活的战斗头脑。\r\n相较大乔，二乔性格活泼机敏，更善于用头脑解决问题；同时为人也更加轻浮。\r\n能够感觉到作者对美国文化和英国传统分野的某种期待", "season_id": 2, "stand": 3}, {"id": 18, "image": "characters/罗吉兹.png", "name": "罗吉兹", "status": "被柱男艾斯迪斯", "side": "丽莎丽莎的仆人", "detail": "莉莎莉莎的部下，负责教导乔瑟夫和西撒的波纹老师。\r\n\r\n严格的教导方式，让乔瑟夫很是受不了。\r\n\r\n然而，在他准备对乔瑟夫进行最终训练的时候，却被艾斯迪斯杀死。\r\n\r\n虽说乔瑟夫·乔斯达很讨厌他的严格教育方式，不过，他还是感谢罗吉兹的教导。", "season_id": 2, "stand": 1}, {"id": 19, "image": "characters/梅西奈.png", "name": "梅西奈", "status": "寿终正寝", "side": "莉莎莉莎的部下", "detail": "和罗吉兹一样，是丽莎丽莎的仆人。\r\n\r\n和罗吉兹一起教导西撒和乔瑟夫的波纹气功的老师。\r\n\r\n十分欣赏西撒·A·齐贝林。\r\n\r\n与西撒进行最终试炼时因为右臂的手毛全被西撒波纹烫光，若不及时中断战斗极有可能出人命甚至二个一起死因此果断认可了对方。\r\n\r\n在乔瑟夫的葬礼上搀扶住了瘫软的史摩基·布朗。\r\n\r\n没有标明其最终结局，可能已经寿终正寝。", "season_id": 2, "stand": 1}, {"id": 20, "image": "characters/丝吉Q.png", "name": "丝吉Q", "status": "先于丈夫JOJO2代去世", "side": "JOJO2代的妻子", "detail": "服侍莉莎莉莎的女仆，意大利人、天然呆。在战斗潮流尾声时与乔瑟夫结婚。\r\n第二部剧情尾声和乔瑟夫、莉莎莉莎移居美国，乔瑟夫和丝吉Q婚后育有独生女荷莉·乔斯达，一伙人更见证了艾莉娜的离世。\r\n第四部依然健在但没有出场，在得知乔瑟夫在日本有私生子后大发雷霆。\r\n承太郎：丝吉Q外婆和老头子结婚61年，第一次愤怒到了极点。\r\n第四部末尾乔瑟夫带回小静后一度以为乔瑟夫又沾花惹草。", "season_id": 2, "stand": 1}, {"id": 21, "image": "characters/史比特瓦根财团.png", "name": "史比特瓦根财团", "status": "驻地 美国 华盛顿DC", "side": "JOJO家族后盾", "detail": "由荒木飞吕彦所创作漫画《JOJO的奇妙冒险》及其衍生作品的登场的架空的基金会。\r\n第一部结束的数十年后、SPW意外的在美洲大陆发现了大片的油田，使其成为了美国乃至世界级的石油大王。靠石油致富的SPW建立了一个巨大的财团，即现在的SPW财团。\r\n\r\n史比特瓦根财团表面上是一个研究医疗、保护自然、调节生态环境等的财团，但很大一部分投资被用在研究超自然现象。这个部属主要研究柱之男和吸血鬼等的关系，第三部之后也开始研究替身，虹村形兆的箭后保存在这个部属。\r\n\r\n史比特瓦根财团在财力、人力、研究等地全方面地支援乔斯达家族，一直延续到第六部结束。", "season_id": 2, "stand": 1}, {"id": 22, "image": "characters/鲁多尔冯修特罗海姆.png", "name": "鲁多尔·冯·修特罗海姆", "status": "战死", "side": "第三帝国", "detail": "德意志第三帝国军人，党卫军二级突击大队长。故事刚开始时让人觉得是个走搞笑路线的小BOSS，其漱口的方式和声音十分魔性，声优辛苦了。\r\n\r\n非常钦佩有勇气的人，即便对方是纳粹极其痛恨的犹太人战俘与敌对国英国的乔瑟夫同样会为之由衷尊重。\r\n\r\n在后来的战斗中，可以看出他其实是一个勇敢、果断、热爱自己的国家和民族，甚至会让二乔为之敬佩的优秀军人。\r\n\r\n在和柱之男桑塔纳的战斗中，修特罗海姆的身体支离破碎，但还是靠着德意志科技存活下来，成为改造人。与此同时，他变得正经且有些中二。\r\n\r\n改造后的修特罗海姆拥有与桑塔纳二倍的力量、速度和身体强度，体温也比普通人低许多，有如金属; 胸膛内有一门甚至能伤到卡兹的重机关炮，右眼是专克柱之男的小型紫外线发射装置; 全身上下也有许多辅助装置。\r\n\r\n死于1943年的斯大林格勒保卫战", "season_id": 2, "stand": 1}, {"id": 23, "image": "characters/桑塔纳.png", "name": "桑塔纳", "status": "石化封存", "side": "柱之男", "detail": "在墨西哥遗迹被SPW财团的考古队发现，落入纳粹德国的手里并进行各种实验。由于德军低估了柱之男的实力，导致其逃出实验场地并杀死了除修特罗海姆和史比特瓦根以外的其他在场人员，正欲对史比特瓦根下手之际乔瑟夫·乔斯达赶到，被乔瑟夫调戏了一番后与乔瑟夫交战。\r\n\r\n乔瑟夫所掌握的波纹之力本该是柱之男一族的克星，但彼时的乔瑟夫并未进行过任何正规的波纹训练，因此其波纹强度尚不足以杀死桑塔纳。最后在乔瑟夫和修特罗海姆的努力下，桑塔纳被引诱到室外的阳光下，因柱之男害怕阳光的特性而在阳光下变成石头，再起不能。", "season_id": 2, "stand": 1}, {"id": 24, "image": "characters/瓦姆乌.png", "name": "瓦姆乌", "status": "死亡", "side": "柱之男", "detail": "柱之男中的一员，万年前卡兹杀死部落的人后带走的两名婴儿之一，以大人称呼卡兹。\r\n\r\n战斗天赋极强。使用流法为“风”，常用技术为“斗技·神砂岚”，是用双手快速旋转制造出强大的旋风攻击敌人的招数。秘技，神砂岚！ 具有极强的破坏力，西撒就是死于这招，必杀技“最终流法·浑楔飒”，是从额头上的洞喷出强大的风压并用其快速击打敌人的招数。此外还使用过甩动头部两侧挂坠，掀起小型真空龙卷风切割敌人的招数。\r\n\r\n讨厌别人踩他的影子\r\n是一名勇敢骄傲的战士，重视荣誉感，最后一战与乔瑟夫惺惺相惜，战败后随风而逝，乔瑟夫在风中向已逝的瓦姆乌敬礼。\r\n名字来源于威猛乐队（Wham!）。", "season_id": 2, "stand": 1}, {"id": 25, "image": "characters/艾斯迪斯.png", "name": "艾斯迪斯", "status": "死亡", "side": "柱之男", "detail": "艾斯迪斯是被称为“柱之男”的暗之一族的一员。卡兹屠杀族人之时，只有艾斯迪斯和卡兹意见相合，暗之一族其他成员除了两个婴儿（瓦姆乌、桑塔纳）外都被二人屠杀殆尽。\r\n\r\n万年前，艾斯迪斯曾经和卡兹一起对付波纹一族，自称已战胜了无数波纹战士。\r\n\r\n拥有极其强大的执念，败阵之后，尽管全身都被波纹化为灰烬，仍然将大脑附着于乔瑟夫背上，并成功地将艾哲红石偷出并送出威尼斯，是个为了达成目标不惜一切的男人。此后更将大脑附着于莉莎莉莎的侍女丝吉Q的身体上，威胁波纹战士们“要杀死自己就要杀死这个小姑娘”，但最终被乔瑟夫和西撒的正负波纹对冲策略彻底消灭。死后，乔瑟夫对艾斯迪斯至死不渝的信念表达了敬意。\r\n\r\n与之前主要依赖各种非人能力而非战略战术的战斗不同，艾斯迪斯与乔瑟夫的战斗中二人都倾尽了各种策略，可说是开启了JOJO系列“智战”特色的先河。\r\n\r\n名字来自于澳大利亚摇滚乐队AC/DC", "season_id": 2, "stand": 1}, {"id": 26, "image": "characters/卡兹.png", "name": "卡兹", "status": "宇宙封存", "side": "柱之男头子", "detail": "卡兹是第二部“战斗潮流”的最终反派。柱之男的一员，在柱之男中地位与智慧排行第一,年龄好像最大。并非是实力最强的柱男，根据6251设定集，卡兹成为究极生物前，瓦姆乌是最强大/最难以应付的敌人。\r\n\r\n使用流法为“光”，必杀技“辉彩滑刀”，可以从双臂的骨骼中延伸出刀刃，能够精准快速地切裂机枪扫射的子弹，刀刃上还能发出强烈的光芒\r\n\r\n拥有身为强者的自尊心。\r\n\r\n是当最后一个柱男同伴逝去后为了成为究极生物报仇的卡兹什么仁义道德都不讲，放弃一切原则并用出一切可能的卑劣战术。\r\n\r\n强大的力量似乎也导致卡兹变得高傲，进而因为轻敌而被乔瑟夫以及一系列意外事件送上太空并放弃思考。", "season_id": 2, "stand": 1}, {"id": 27, "image": "characters/莉莎莉莎.png", "name": "莉莎莉莎", "status": "与某美国作家再婚寿终正寝", "side": "教导乔瑟夫与西撒波纹的师父，波纹实力极强", "detail": "戴着的围巾是用萨蒂婆罗吉亚甲虫中的肠道晒干后的丝编成的围巾，一条就要用3万只甲虫的肠道。能够100%传递波纹，亦可以用作探测敌人的雷达，传递波纹时更有足以支撑莉莎莉莎的力度。\r\n\r\n虽然看起来只有二十几，实际上已经五十岁了，是靠着波纹气功维持年轻。\r\n\r\n在意大利的威尼斯拥有一座私人岛屿，似乎是波纹战士代代传下来的修练场。貌似还没到秧歌star年代就没了，估计是毁于二战\r\n\r\n与乔瑟夫母子相认后带上乔瑟夫以及儿媳妇兼女仆丝吉Q搬去美国，1948年与一名美国剧作家再婚。\r\n\r\n名字出自乐队Lisa Lisa and Cult Jam。", "season_id": 2, "stand": 1}, {"id": 28, "image": "characters/空条承太郎.png", "name": "空条承太郎", "status": "死亡", "side": "三代JOJO", "detail": "空条承太郎是荒木飞吕彦所创作的漫画《JOJO的奇妙冒险》及其衍生作品的登场角色。第三部「星尘斗士」的主角。\r\n拥有强壮的人形替身白金之星。与外公2代JOJO共同探寻替身奥秘，远渡重洋来到埃及大战一代复活恶敌迪奥拯救母亲，成为最强替身使者。\r\n1999年时，承太郎已经成为了一名著名的海洋生物学家。\r\n2001年，为了调查迪奥的儿子汐华初流乃，承太郎委托康一前往意大利进行调查。\r\n第六部为了女儿身亡", "season_id": 3, "stand": 4}, {"id": 29, "image": "characters/花京院典明.png", "name": "花京院典明", "status": "被DIO穿腹杀害", "side": "星尘十字军", "detail": "总是穿着一身绿色的学生装，甚至在做日光浴的时候也不脱下。非常喜欢吃樱桃，能做出将樱桃用舌头拨来拨去的特技这一点也被黄色节制假扮的花京院还原了然而真正的花京院在吃樱桃时不会有口水。\r\n花京院典明在埃及旅行时遇到了迪奥·布兰度，被其实力所震慑，在极大的恐惧下精神屈服被种植了肉芽，被派去消灭承太郎一行人。\r\n被解救后加入了一行人的埃及之旅最终光荣战死", "season_id": 3, "stand": 5}, {"id": 30, "image": "characters/穆罕默德阿布德尔.png", "name": "穆罕默德·阿布德尔", "status": "死亡", "side": "星尘十字军", "detail": "阿布德尔的性格介于花京院与波鲁纳雷夫之间，他不如花京院小心谨慎，但是又比波鲁纳雷夫沉稳得多。他很信赖自己的伙伴，并且会为了伙伴赴汤蹈火，当然波鲁纳雷夫他们对他也一样。\r\n\r\n在承太郎对决赌徒达比时，阿布德尔说：“我这个人没什么耐性，很不适合赌博，如果我和你玩的话，大概马上就会输吧……但是我相信承太郎，虽然我不晓得这盖着的五张牌有什么意义，但承太郎既然要我赌，我就相信他。不管是我的灵魂还是其他什么东西，我都会和你赌。”\r\n\r\n在深入迪奥的隐藏地时，阿布德尔和波鲁纳雷夫，伊奇约好，互相不去救对方，一心打倒迪奥。但当他们两个身处险境时，他还是出手相救。虽然阿布德尔的戏份不多，但他仍然是JOJO的一位可靠伙伴。\r\n\r\n此外，他还对很多替身的知识有所了解，帮助众人分辨敌人的替身。\r\n\r\n名字来自于被称为“阿拉伯的艺术家”的歌手Mohammed Abdu", "season_id": 3, "stand": 6}, {"id": 31, "image": "characters/简皮耶尔波鲁纳雷夫.png", "name": "简·皮耶尔·波鲁纳雷夫", "status": "肉体死亡，灵魂在Coco Jumbo替身", "side": "星辰十字军", "detail": "波鲁纳雷夫是个天生的替身使者，自幼就勤于锻炼剑术并将替身能力发挥到极致，因为早年丧母，与妹妹雪莉相依为命，3年前妹妹在路上被某个恶徒奸杀，波鲁纳雷夫为寻找杀死妹妹的凶手而四处旅行，被DIO用肉芽控制派去香港刺杀jojo一行。\r\n被解救后加入承太郎一行最终战胜迪奥。\r\n后与承太郎、乔瑟夫告别，先是回到法国的故乡，之后又开始调查弓与箭。\r\n第五季再次出现在由于在和迪亚波罗的战斗中失去了双腿和一只手，银色战车的双腿和手也变成了残肢状态。\r\n\r\n临终前昔日星尘十字军壮烈的一幕幕场景以及替身「审判」如走马灯般闪过脑海并把“箭”交给银色战车，由于其强烈的意念以及肉体的死亡，导致替身暴走并在“箭”的影响下进化成“银色战车镇魂曲”。波鲁纳雷夫则与乌龟Coco Jamboo暂时互换了灵魂，与特里休护卫队的众人碰面并给他们讲解了一些东西。\r\n\r\n最后由于镇魂曲的作用，灵魂得以保留在乌龟Coco Jamboo的替身中，并成为热情组织的参谋。", "season_id": 3, "stand": 7}, {"id": 32, "image": "characters/伊奇.png", "name": "伊奇", "status": "战死", "side": "jojo一行", "detail": "伊奇是一只流浪狗，起初完全无意与乔斯达一行合作，但后期为击败DIO作出了重大而惨烈的贡献。\r\n\r\nJOJO的“人类赞歌”主题在伊奇的身上第一次扩展到了人类之外具有“黄金精神”的生物中。\r\n\r\n名字出自音乐人Iggy Pop（不是Iggy Azalea），而之所以将伊奇塑造成狗的形象又似乎与其单曲《I Wanna Be Your Dog》有关联。并且其替身选择The Fool或许参考了Iggy Pop的专辑《The Idiot》。", "season_id": 3, "stand": 8}, {"id": 33, "image": "characters/格雷弗莱.png", "name": "格雷·弗莱", "status": "死亡", "side": "迪奥派来的杀手", "detail": "在航班上伪装成乘客袭击主角团。曾用替身制造过多起意外事故来夺取旅行者们的财物。\r\n\r\n外表像慈祥的老爷爷实际上残忍卑鄙。在与主角团的战斗中替身被绿色法皇击败，本体也随之而死亡。", "season_id": 3, "stand": 9}, {"id": 34, "image": "characters/假提尼尔船长.png", "name": "假提尼尔船长", "status": "死亡", "side": "迪奥杀手之一", "detail": "在主角团乘坐的轮船上以船长身份出现，实际上是迪奥的手下，杀死了真正的船长并假冒成船长，本名不详。\r\n\r\n临死前用事先准备好的炸弹引爆了主角团乘坐的轮船。\r\n\r\n比平常人的肺活量高三倍，个人的潜水最高纪录是6分12秒。\r\n\r\n名字出自轻摇滚组合Captain & Tennille。", "season_id": 3, "stand": 10}, {"id": 35, "image": "characters/FOREVER.png", "name": "FOREVER", "status": "死亡", "side": "迪奥杀手之一", "detail": "一只具有高度智商的猩猩，最后被白金之星用白金指刺弹中的钮扣击伤。是系列作中出现的第一个完全非人类的替身使者。\r\n\r\n主角团登上第二艘船上后发现的猩猩，拥有很高的智商而且非常好色。控制轮船袭击主角团，并且杀死了除主角团以外的所有人（不包括安）。被承太郎发现后展开战斗，被白金之星击败。\r\n\r\n死前曾露出肚子示意认输，但是还是被空条承太郎的白金之星欧拉至死。", "season_id": 3, "stand": 11}, {"id": 36, "image": "characters/诅咒的迪波.png", "name": "诅咒的迪波", "status": "死亡", "side": "迪奥杀手之一", "detail": "在酒店袭击波鲁那雷夫，发动替身能力后故意被波鲁那雷夫用替身击伤，自己跳楼逃生并躲进了某个厕所内，借替身操纵房间内的一个木偶袭击波鲁那雷夫。\r\n\r\n后来波鲁那雷夫击败了被附身的木偶，用银色战车戳爆了木偶全身，诅咒的迪波也随之死亡。", "season_id": 3, "stand": 12}, {"id": 37, "image": "characters/拉巴索.png", "name": "拉巴索", "status": "被空条承太郎痛殴下落不明", "side": "迪奥杀手之一", "detail": "在新加坡出现，变成花京院典明的模样袭击承太郎。非常自恋，认为自己的替身是无敌的。被承太郎拖入水中，因无法呼吸而被迫解除替身。被承太郎痛打后求饶，供出了其他几名被DIO雇佣的替身使者后，偷偷移动到下水道井盖边，通过下水口暗算仍在水中的承太郎，却被白金之星从下水口打飞的井盖弹飞。落入水中后试图再次向承太郎求饶，却被被激怒的承太郎用白金之星一顿痛殴。", "season_id": 3, "stand": 13}, {"id": 38, "image": "characters/J盖尔.png", "name": "J·盖尔", "status": "死亡", "side": "迪奥杀手之一", "detail": "数年前性侵并杀害了许多女性，其中就包括波鲁纳雷夫的妹妹，是导致波鲁纳雷夫以复仇为由加入主角团，并参与对抗迪奥的主要原因。\r\n\r\n性格卑鄙、狡猾，很少以真面目示人，受到迪奥的命令在印度与荷尔·荷斯搭档迎击承太郎等人。起初在印度某间饭店的厕所里袭击了波鲁纳雷夫，波鲁纳雷夫因复仇心切而离开主角团，并在遭遇荷尔·荷斯后与其交战，在阿布德尔加入战斗后，利用躲在积水反光中的替身偷袭了阿布德尔，再由荷尔·荷斯的皇帝打中头部将其击倒。\r\n追击波鲁纳雷夫和随后赶到的花京院典明，将他们逼入一个贫困的村庄里，将替身藏在难民的眼睛里对两人展开攻击。\r\n\r\n最后被波鲁纳雷夫用替身穿刺数次而死。", "season_id": 3, "stand": 14}, {"id": 39, "image": "characters/荷尔荷斯.png", "name": "荷尔·荷斯", "status": "被自己的子弹击中，再起不能", "side": "迪奥杀手之一", "detail": "与三部很多反派不同，荷尔·荷斯不但不是DIO忠诚的仆人，而且还曾经主动试图杀掉DIO。但是见证了DIO的替身“世界”的恐怖之处时停钻裆之后，荷尔·荷斯还是因为惧怕而继续为DIO做打手。", "season_id": 3, "stand": 15}, {"id": 40, "image": "characters/妮娜.png", "name": "妮娜", "status": "因为替身被破坏而死", "side": "迪奥杀手之一", "detail": "妮娜出身自一个贵族家庭，被迪奥雇来拦截并杀死主角团。她利用替身能力杀害一位印度美女后伪装她的样子，随后利用波鲁那雷夫的见色起意的性格来暗杀乔瑟夫·乔斯达。\r\n\r\n他将替身寄生在乔瑟夫身上，本体却勾引波鲁那雷夫使乔瑟夫无法得到帮助。后来乔瑟夫设法将女帝困住后击败，本体也在快要吻波鲁那雷夫时随替身的破坏而死亡。\r\n\r\n目睹惨剧、得知真相的波鲁那雷夫落寞地坐在了地上，欲哭无泪。", "season_id": 3, "stand": 16}, {"id": 41, "image": "characters/ZZ.png", "name": "ZZ", "status": "被击败不见踪影", "side": "迪奥杀手之一", "detail": "迪奥雇佣的替身使者，在巴基斯坦袭击主角团。只有手臂非常发达，躲在车里伸出手臂唬人，实际上很瘦弱。\r\n\r\n在山地追击主角团，最后被白金之星击败。\r\n\r\n然后被众人惩罚，之后下落不明。", "season_id": 3, "stand": 17}, {"id": 42, "image": "characters/恩雅婆婆.png", "name": "恩雅婆婆", "status": "失败后被肉芽杀死", "side": "迪奥忠仆", "detail": "当初在DIO的身旁献策派遣刺客刺杀乔斯达一行被DIO所接纳，但是，最终包含她儿子在内的7人都以失败告终，她也因儿子丧生、极度愤怒的缘故而摧毁了自己的面目，并只身前往复仇。\r\n\r\n使用替身能力将墓场幻化为小镇并伪装成旅馆的主人[1]，在大厅里袭击了荷尔·荷斯，找来空条承太郎等人住宿时因对方使用了假名而不小心唤出真名导致差点露陷，最终以团队中有人呼唤过承太郎的真名而蒙混过关。随后因荷尔·荷斯的缘故她又袭击了波鲁那雷夫并将其逼至厕所，为了替儿子报仇而使其露出一系列惨相，当她袭击承太郎时被对方发现异样，最后欲以替身伤害承太郎时被对方的白金之星吸尽了替身导致窒息晕倒。\r\n\r\n被想打探DIO信息的乔斯达一行人所擒，最终被恋人在体内种植肉芽封口杀害[2]。但是直到最后对DIO的忠心仍未改变，到死仍为其保守秘密。。", "season_id": 3, "stand": 18}, {"id": 43, "image": "characters/钢铁阿丹.png", "name": "钢铁阿丹", "status": "推测已死，至少残废", "side": "迪奥杀手之一", "detail": "奉迪奥之命为了不让恩雅婆婆泄露迪奥的秘密而来到卡拉奇，用替身杀死恩雅婆婆后将替身寄生在乔瑟夫脑部，以乔瑟夫为人质并戏弄承太郎。平常语气和善，一旦得意忘形就会变得粗鄙不堪。\r\n\r\n后来替身在乔瑟夫体内被绿色法皇和银色战车击败，最后被愤怒的承太郎打飞", "season_id": 3, "stand": 19}, {"id": 44, "image": "characters/阿拉伯胖子.png", "name": "阿拉伯胖子", "status": "击晕下落不明", "side": "迪奥杀手之一", "detail": "在阿拉伯袭击主角团。\r\n\r\n本体躲在镜子后面隐蔽自己，同时放出替身来迎战。由于其用于隐蔽自己的办法过于愚蠢，反而让承太郎等人始料未及，因此导致主角团在找不到太阳本体的情况下陷入困境。\r\n\r\n后来其踪迹被承太郎一行人通过两块左右相对的石头发现，看穿其拙劣把戏的承太郎一行人（除了尚且蒙在鼓里的乔瑟夫·乔斯达）皆大笑了起来。随后阿拉伯胖子被白金之星用一块石头砸晕。", "season_id": 3, "stand": 20}, {"id": 45, "image": "characters/Mannish_Boy.png", "name": "Mannish Boy", "status": "被主角团送到别人家收养", "side": "迪奥杀手之一", "detail": "搭乘主角团飞机的婴儿，长着吸血鬼一样的獠牙，能用哭声操控他人，虽然只有11个月大但是却有成年人一般的智商。\r\n\r\n以婴儿的外表迷惑了主角团，但是却遭到花京院典明一个人的怀疑。后来在沙漠里趁主角团睡着时在梦里攻击主角团，最后被花京院典明击败，并且还品尝了一碗美味粥。\r\n\r\n最后被主角团送往别的城镇。", "season_id": 3, "stand": 21}, {"id": 46, "image": "characters/卡梅欧.png", "name": "卡梅欧", "status": "烧成重伤", "side": "波鲁那雷夫仇人", "detail": "躲在红海的小岛里。\r\n\r\n控制用泥土制造的阿布德尔和波鲁那雷夫的妹妹来袭击对妹妹和阿布德尔的死而心中有愧的波鲁那雷夫。\r\n\r\n最后替身被真正的阿布德尔打败。\r\n\r\n踪迹被发现，被阿布德尔和波鲁那雷夫整蛊了一番惨遭喂尿，早期阿帕茶，狼狈不堪地出来后又被阿布德尔用魔术师之红的火焰重创。\r\n。", "season_id": 3, "stand": 22}, {"id": 47, "image": "characters/蜜朵拉.png", "name": "蜜朵拉", "status": "牙齿全部被打没", "side": "迪奥忠诚部下", "detail": "在非洲大陆上用替身袭击乘坐潜艇横穿非洲海峡的承太郎一伙，利用潜艇内存在的各种无机物将其压制。\r\n\r\n承太郎一行人无奈之下弃船逃跑，却反而落入其圈套——蜜朵拉直接在海底造出一张巨大鬼脸将其全部吞下。在准备杀死承太郎等人前声称“承太郎是自己喜欢的类型，所以把他杀掉很可惜”。承太郎一行人为了寻找机会逃跑假意奉承其美貌，反而将其彻底激怒，准备用替身的牙齿碾碎承太郎。结果其牙齿直接被承太郎凭借白金之星的蛮力全部打爆，就此被击败。\r\n\r\n承太郎一行人登陆后发现了再起不能的蜜朵拉，让波鲁纳雷夫前去调查其情况，结果得到的回复是——“牙齿全部被打没了！实在没脸看啊！。", "season_id": 3, "stand": 23}, {"id": 48, "image": "characters/恩多尔.png", "name": "恩多尔", "status": "用替身自尽", "side": "迪奥忠诚部下", "detail": "曾经是个依靠替身横行霸道的恶徒，在遇到迪奥后，受迪奥的人格魅力所折服，成为了迪奥的忠实部下。\r\n\r\n恩多尔不敌承太郎，被白金之星击中腹部而无力再战。为了不被乔瑟夫的隐者之紫挖出有关DIO的情报而选择用替身自尽，临终前告诉承太郎自己的名字，并透露了九名新的替身使者的替身源自塔罗牌的起源——埃及九荣神，而他的替身是九荣神暗示的大地之神“盖布(Geb)”。之后，象征灵魂的白烟从他身上散去，水状替身也永远地渗入沙中。\r\n\r\n这个忠诚、有信念而又十分强劲的替身使者受到空条承太郎的尊敬，他将恩多尔的遗体就地埋葬，并用恩多尔的拐杖为其立碑。。", "season_id": 3, "stand": 24}, {"id": 49, "image": "characters/欧因哥.png", "name": "欧因哥", "status": "被炸弹炸伤且被打进医院，再起不能", "side": "迪奥杀手之一", "detail": "和弟弟波因哥一起行动，在亚斯文袭击主角团。\r\n\r\n沉不住气，但是很为弟弟着想。\r\n\r\n屡次暗杀主角团都失败，于是按照托托神的预言指示变成了承太郎的模样，打算用橙子炸弹暗杀主角团，却反而把自己炸伤，最后遭人报复被揍到住院。", "season_id": 3, "stand": 25}, {"id": 50, "image": "characters/波因哥.png", "name": "波因哥", "status": "住院", "side": "袭击主角团杀手", "detail": "在亚斯文袭击主角团。\r\n\r\n内向怕生，不善于和哥哥以外的人打交道。屡次暗杀主角团失败，后来和欧因哥一起被人报复而揍到住院。\r\n\r\n之后荷尔·荷斯为了击败空条承太郎等人，于是把他绑架到开罗，并要求他联合自己来袭击主角团。\r\n\r\n失败后打算做个好孩子，却被因为不小心乱踢箱子砸到了伊奇，导致伊奇被砸出包而很愤怒，从而被伊奇咬伤。因此，他的性格变的更加阴暗了。", "season_id": 3, "stand": 26}, {"id": 51, "image": "characters/阿努比斯.png", "name": "阿努比斯", "status": "生锈报废并且放弃了思考", "side": "迪奥杀手之一", "detail": "阿努比斯的本体是五百年前一名叫恰拉邦·萨拉伊的刀匠，死后其替身附在了一把刀上[3]，并成为现在的阿努比斯神。\r\n\r\n这把刀先前被保存在博物馆内，后被迪奥·布兰度发现并释放。在和迪奥的“世界”交手后屈服于对方的强大，因此臣服了迪奥。\r\n\r\n曾控制很多人袭击主角团，先控制一位农家青年恰卡弑父后袭击波鲁那雷夫，被波鲁那雷夫用弹射剑芯的秘技击败。\r\n\r\n阿努比斯在慌乱中不小心使出了自己的穿墙能力，就这么穿过墙飞到了河上，击中船上一头牛的屁股。阿努比斯神意图控制牛但失败了，牛在挣扎之中落水，刀尖沉入河底，水里的动物都不搭理他的呼唤或被吓跑，阿努比斯神就这么在水底放弃了思考。", "season_id": 3, "stand": 27}, {"id": 52, "image": "characters/玛莱雅.png", "name": "玛莱雅", "status": "全身骨折入院，再起不能", "side": "迪奥杀手之一", "detail": "DIO派去阻止星尘十字军的替身使者之一\r\n最后被阿布德尔和二乔前后夹击，玛莱雅利用电线攻击二人，二人反利用磁力用一堆铁将其夹到全身骨折住院。", "season_id": 3, "stand": 28}, {"id": 53, "image": "characters/阿雷西.png", "name": "阿雷西", "status": "被打飞至遥远的地方，再起不能", "side": "迪奥杀手之一", "detail": "开场时得知一个孩子的父母不在附近时揍了那个孩子。能力时把人年轻化到婴儿。\r\n\r\n把波鲁纳雷夫变小后，追杀变小的波鲁那雷夫，一个女人把波鲁纳雷夫带进了自己家，阿雷西把那个女人幼化成胚胎。之后被波鲁那雷夫设法打败，逃出来后又趁机袭击承太郎，又被变小的承太郎打败。最后被恢复原状的两人打飞。", "season_id": 3, "stand": 29}, {"id": 54, "image": "characters/丹尼尔J达比.png", "name": "丹尼尔·J·达比", "status": "被承太郎吓至精神失常", "side": "迪奥杀手之一", "detail": "承太郎一行人在寻找迪奥藏匿的建筑物时来到了达比所在的酒馆，达比说自己知道建筑物的位置，但是需要和他赌博。\r\n当对方需要说出“我拿我的灵魂来赌”或者立下字据等凭据，之后如果对方输了，达比的奥西里斯神就会直接抽出他的灵魂，做成筹码。即使是不说出来，在心中认输也是一样。奥西里斯神还能将灵魂筹码分成数份进行赌博。承太郎最终靠自己的智力与冷静击败了达比。\r\n\r\n是JOJO系列中第一个在心理战中落败而再起不能的角色。", "season_id": 3, "stand": 30}, {"id": 55, "image": "characters/佩特夏.png", "name": "佩特夏", "status": "死亡", "side": "迪奥宠物", "detail": "迪奥饲养的一只隼，看守着迪奥的洋馆，会杀死任何接近城堡的生物。\r\n\r\n伊奇在城堡前目睹它杀死了两只大狗和前来调查城堡的男子，对其实力感到恐惧，为了保命而装成事不关己的路过野狗蒙混过关，最终仍因为救下被杀死的大狗的小孩饲主而遭到佩特夏追杀。佩特夏从下水道不断逼迫伊奇并且冻结它的左前腿，伊奇于是放弃自己的腿逃进湖里，制作了一个沙之堡垒要塞躲藏和抵挡攻击，但佩特夏不但冻结沙之堡垒周围的水，还预料到伊奇会挖洞离开，在河底下的地层中堵路佩特夏：这里已经满员了并准备从口部发射冰弹以终结无路可逃的伊奇，最后伊奇在情急之下决定孤注一掷，利用沙之堡垒破碎时产生的冲击向冲向前咬住了佩特夏的喙，让其因为无法发射冰弹而炸膛而死", "season_id": 3, "stand": 31}, {"id": 56, "image": "characters/泰伦斯T达比.png", "name": "泰伦斯·T·达比", "status": "被承太郎揍飞而再起不能", "side": "迪奥宅邸管家", "detail": "玩家达比会与对手进行游戏比赛，如果对方被击败或者在心中认输，阿图姆神就会抽走对方的灵魂做成人偶，因其读心能力战无不胜，但最终被承太郎和乔瑟夫双簧玩法7片击败。", "season_id": 3, "stand": 32}, {"id": 57, "image": "characters/肯尼G.png", "name": "肯尼·G", "status": "再起不能", "side": "迪奥手下", "detail": "和小达比、瓦尼拉·艾斯一起作为Dio最后的防线出场，任务是阻止承太郎等人。躲藏在柱子里，利用替身在屌魔馆内制造幻觉，将整个屌魔馆变成了一座迷宫，小达比的游戏场地也是他制造的。最后本体被阿布德尔的替身能力探查到，被伊奇打倒。", "season_id": 3, "stand": 33}, {"id": 58, "image": "characters/大笨蛋.png", "name": "大笨蛋", "status": "被迪奥时停后关入棺材中分尸", "side": "迪奥手下", "detail": "曾对迪奥开枪测试迪奥的替身能力。\r\n\r\n因为实力太弱的缘故，被同属迪奥亲卫队的瓦尼拉·艾斯、泰伦斯·T·达比和肯尼·G嫌弃，不让他与三人一同出击。瓦尼拉等人被打败后，用特殊能力伪装成一个女人想要欺骗承太郎等人，但却被很快看穿，被承太郎当做测试吸血鬼抗击打能力的工具，并反被要挟带路前往迪奥的藏身处。\r\n\r\n最后在打开棺材时被迪奥时停放进了棺材中，身体被切成数块", "season_id": 3, "stand": 1}, {"id": 59, "image": "characters/瓦尼拉艾斯.png", "name": "瓦尼拉·艾斯", "status": "在阳光的照射下死亡", "side": "迪奥忠仆", "detail": "迪奥的忠实手下，因为忠于迪奥而把自己的头砍下来打算把血捐给他，因此受到迪奥的赏识而被变成吸血鬼。最初DIO只能制造尸生人，后来卷土重来后独自强化复活术来制造吸血鬼。\r\n\r\n出场一瞬间就秒杀穆罕默德·阿布德尔，但由于阿布德尔用命推开波鲁那雷夫与伊奇令他原本一箭三雕的偷袭以失败告终，上楼后挖掉波鲁那雷夫的脚趾剥夺其行动力。伊奇用沙子伪装成迪奥试图从背后偷袭他，识破后用本体拳脚重创伊奇打到濒死。\r\n\r\n危机关头伊奇舍命发动最后一次替身将波鲁那雷夫救下，使波鲁那雷夫得以反击瓦尼拉，并看穿瓦尼拉已成为吸血鬼的事实。\r\n\r\n之后因银色战车斩开窗户入来的阳光照射而死。", "season_id": 3, "stand": 34}, {"id": 60, "image": "characters/荷莉乔斯达.png", "name": "荷莉·乔斯达", "status": "健在", "side": "3代JOJO母亲", "detail": "空条荷莉，旧姓乔斯达，是乔瑟夫·乔斯达和丝吉Q的女儿，空条承太郎的母亲，丈夫是日本音乐家空条贞夫但是全书中他并没有出场。\r\n\r\n和乔瑟夫·乔斯达、穆罕默德·阿布德尔一起把因为觉醒替身能力而不愿离开监狱的空条承太郎带出了监狱。\r\n\r\n在空条承太郎将花京院典明带回家后，突然发了高烧病倒。原来，这是DIO所下的咒缚，而荷莉之前为了不让人担心一直在隐瞒自己的情况，于是荷莉的病成为了承太郎等人前往埃及寻找DIO的契机第三部的故事也由此展开。", "season_id": 3, "stand": 1}, {"id": 61, "image": "characters/罗杰斯.png", "name": "罗杰斯", "status": "健在", "side": "2代jojo仆人", "detail": "罗杰斯在纽约帮着乔瑟夫·乔斯达对丝吉Q隐瞒荷莉重病一行人前去打败DIO的事，谎称乔瑟夫忙于工作前往外地，并一直服侍着丝吉Q。乔瑟夫有时也会通过罗杰斯了解史比特瓦根财团以及荷莉的情况。", "season_id": 3, "stand": 1}, {"id": 62, "image": "characters/安.png", "name": "安", "status": "离家出走", "side": "亚洲少女", "detail": "星尘十字军在从上海通往新加坡的史比特瓦根财团的船上认识的路人少女。偷偷混入了船上并被水手发现。之后被假提尼尔船长挟持并被空条承太郎解救。\r\n\r\n假提尼尔船长引爆船后，一行人登上替身船「力」后，在洗澡时被猩猩FOREVER袭击，空条承太郎赶到并保护了她。\r\n\r\n后来，她跟随着空条承太郎等人经历了很多的战斗。\r\n\r\n在漫画里，在「命运车轮」战打败ZZ后就没有戏份了，动画里加了在巴基斯坦打败ZZ后坐飞机与星尘十字军离别的剧情。", "season_id": 3, "stand": 1}, {"id": 63, "image": "characters/吉良吉影.png", "name": "吉良吉影", "status": "被救护车杀死", "side": "横行于杜王町的连续杀人魔", "detail": "吉良吉影是武士家族的末裔，但是家族传承至祖父那代时，家世已没落。\r\n根据本人所述，儿时因为偶然从画册里看见意大利著名画家达·芬奇的名作《蒙娜丽莎的微笑》，意外产生了生理反应boki，以此契机发现自己对女性手部的迷恋执着。成年后继承祖先留下的宅邸，任职于龟友连锁店。\r\n1983～1999年间至少杀害了四十多位女性（对女医生发言时自称48名）。\r\n空条承太郎与广濑康一根据矢安宫重清所留下的线索，被追查逼至修改身份，然而杀人的欲望难以控制终于暴露自己的踪迹，最终被众人围困，妄图发动败者食尘，在按下按钮的前一刻被广濑康一用替身能力强制把手按地上，被承太郎时停击飞，被救护车碾轧身亡，灵魂也被『不能回头的小巷』抓走。", "season_id": 4, "stand": 35}]}
    const stdinfo = {"stands": [{"id": 1, "image": "stands/乔纳森乔斯达.png", "name": "没有替身", "detail": "替身出现在大乔死后一百多年"}, {"id": 2, "image": "stands/世界.png", "name": "世界", "detail": "世界是DIO的替身，为近距离力量型替身，凭借着高强度的力量速度和时间停止在对战上有很大的优势。\r\n世界是个壮硕的人型替身，从外观上看来与DIO非常相似。它戴着头饰，遮住了鼻子的底部，其脸庞和三角形的倒像有点像埃及的红色王冠。它的背面配有一个小型简单的双潜水气瓶，并通过一条短的双脊形气管连接到面罩的背面。从腰部的后部到肩膀的前部，都用厚的膨化罗纹皮带固定。下巴，腹部和膝盖有心形装饰。\r\n\r\n在漫画官方上色版中，“世界”完全是黄色的。在动漫中，它具有灰色的皮肤，绿色的心形图案和金色的衣服，跟DIO的服装十分类似。在OVA中，“世界”是带有青铜心形图案和象牙色衣服的灰色皮肤。"}, {"id": 3, "image": "stands/隐者之紫.png", "name": "隐者之紫", "detail": "主体为紫色，替身外表看似荆棘一样的植物。\r\n\r\n扣除在替身登场前就已经死亡的乔纳森，隐者之紫是历代JOJO的替身中唯一的非人型替身，尽管乔瑟夫在获得替身时已不再是主角。"}, {"id": 4, "image": "stands/白金之星.png", "name": "白金之星", "detail": "主体为蓝紫色，外观为留着长发的壮硕男性，荒木表示，因为是主角的替身，所以设定成简单又能打的战斗型，造型上是采用守护神的感觉去设计的。\r\n白金之星没有其他特殊能力，但作为代替拥有超强的破坏力、精密度、速度，且拥有很多利用物理面板攻击的小技能。视力、学习能力很强，会说话，有一定自主意识。\r\n在第三部的最后与DIO的战斗中觉醒的能力。和DIO的世界一样拥有停止时间的能力。 第三部结尾能够停止5秒，第四部因为十年来未曾遇到过需要使用时停的对手或情况而未使用过时停而弱化到0.5秒~2秒，第六部恢复至年轻时水平，为2秒~5秒。\r\n替身名分别出自塔罗牌大阿卡娜牌的17号「星星（The Stars）」，代表了希望与祝福，以及音乐唱片销售认证授予专辑的称号之一「白金」。"}, {"id": 5, "image": "stands/法皇之绿.png", "name": "法皇之绿", "detail": "法皇之绿是JOJO的奇妙冒险第三部角色花京院典明的替身。\r\n\r\n法皇之绿是作中远程操作型替身之祖。\r\n\r\n主体绿色。全身布满像哈密瓜般的细密纹路，穿着浅白色盔甲，脸上戴着嘴套。常态为人型，但其实是由无数的触手合体而成的。因此可以将全身份解成超长的触手来攻击，也有将体液凝固之后发射出的招数“绿宝石水花”。\r\n荒木为了让敌我双方的上色看起来平衡协调而将替身主体设计成绿色，就如同承太郎那句「像极了哈密瓜」，而荒木创作第三部的时期刚好也迷上绿色，画集采用了大量绿色色调。\r\n\r\n替身名出自塔罗牌大阿卡娜牌的5号「教皇（The Hierophant）」，象征天地之间的桥梁，与替身的远程能力有关。"}, {"id": 6, "image": "stands/魔术师之红.png", "name": "魔术师之红", "detail": "主体红色，外型为壮硕的鸟头男子，腿有时被燃烧的火焰所覆盖。两只手腕上都戴着黑色的手镯，最初的红色魔术师是赤脚的，但在阿布德尔归队后变成穿着阿拉伯鞋。\r\n替身外型参考了不死鸟，但可能也受到法国漫画家恩奇·毕拉的作品《尼可波勒三部曲》的影响，也有可能来自于埃及国宝鲸头鹳，还有可能受到了埃及神明拉的影响。荒木在JOJOVELLER对替身的评价中表示，当第三部主角群的替身们站成一排时，为了不让魔术师之红被挡住，才给它设计了像鸟一样的头部。"}, {"id": 7, "image": "stands/银色战车.png", "name": "银色战车", "detail": "主体银色，外型类似穿着中世纪盔甲的瘦小机器人，肩垫和肘部都附带重型装甲。头盔前额嵌有一颗宝石，\r\n\r\n在第五部中，由于波鲁纳雷夫在与迪亚波罗的战斗中被他重创，导致肢体残疾，银色战车相应部分也被金属钉状的附属物所取代。\r\n\r\n在构思上荒木考虑到白金之星是人型替身，加上认为与拳头相对应的是刀剑，因此银色战车是采用了西洋盔甲融合机器人样貌的替身，同时也符合荒木理想中速度敏捷的王道漫画角色形象。\r\n\r\n替身名出自塔罗牌大阿卡纳牌的7号「战车（The Chariot）」，代表了胜利、征服、势力与冲突。"}, {"id": 8, "image": "stands/伊奇.png", "name": "愚者", "detail": "主体在原作官方上色是金色，动画版则是银色，愚者拥有机械动物的外型，与类似汽车底盘的东西结合在一起，前脚是机械兽爪，后脚部分则是轮子，头部造型类似部落土著的面具。\r\n\r\n荒木表示，愚者是野兽和汽车的混合体，而沙漠作为伊奇的初登场舞台，也因此让这个替身拥有美洲土著的元素。\r\n\r\n替身名出自塔罗牌大阿卡娜牌的0号「愚者（The Fool）」，代表了旅行、新的开始、纯真与自发性。"}, {"id": 9, "image": "stands/灰塔.png", "name": "灰塔", "detail": "灰塔是格雷·弗莱的替身。格雷·弗莱曾利用灰塔制造过一起又一起的凶杀案件，是第三部中第一个真正意义上的敌对替身，在动画中只有半集的戏份。\r\n灰塔的外形和大小与锹形虫一致，有着乌黑的甲壳和蓝色的花纹，扇动翅膀时的声音如同黄蜂一般。灰塔的口中藏有一个可以自如伸缩的第二口器“塔针”。\r\n替身名则出自出自塔罗牌大阿卡娜牌的16号「塔（The Tower）」，象征着旅途的终止[1]，而灰塔确实成功地阻止了星尘十字军乘坐飞机直接前往开罗的旅途。\r\n\r\n由于「The Tower」是塔罗牌大阿卡娜牌中最不受欢迎的一张，荒木飞吕彦便选择将象征着「The Tower」的灰塔作为第一个完全敌对的替身，而将灰塔的形态设计成昆虫的想法则是由于荒木本人认为在飞机这样的封闭空间中飞来飞去的苍蝇非常烦人。\r\n\r\n剧情中灰塔所制造的在英国坠机的事件很可能是源自1988年泛美航空103航班的坠机事件，该次事件中，客机在经过英国国境上空时遇袭并被击落，270人在本次事件中丧生。1988年同样也是《星尘斗士》的故事发生的年份。"}, {"id": 10, "image": "stands/暗蓝之月.png", "name": "暗蓝之月", "detail": "暗蓝之月有着鱼人般的外表，全身覆盖着浅蓝色的湿滑皮肤，肌肉部分有暗蓝色的鱼鳞，扁平的头部生有两对眼睛和暗红色的斑纹，背上和双腿生有巨大而锋利的鱼鳍，关节部分长有可以自由开闭的鳃。巧合的是，真的有一种天文现象叫“蓝月亮”。\r\n\r\n替身名出自塔罗牌大阿卡娜牌的18号「月亮（The Moon）」，象征着对未知世界的恐惧[1]，而牌面上绘制的水塘则代表着潜意识，鳌虾自水塘中前往地面则象征着摆脱恐惧与谎言。\r\n\r\n暗蓝之月的外形是基于荒木“将有机物和矿物相结合”的想法诞生的，同时似乎也有参考电影《黑湖妖谭》。暗蓝之月鱼类般的外形和水战能力似乎是启发自塔罗牌上的水塘。"}, {"id": 11, "image": "stands/力.png", "name": "力", "detail": "力的外形犹如一艘巨大的废弃油轮，是JOJO系列迄今为止登场替身中体型最大的替身。\r\n\r\n替身名则出自出自塔罗牌大阿卡娜牌的8号「力量（Strength）」，象征着战斗的挑战、坚强的意志和隐藏的潜力。\r\n\r\n这个替身的灵感来自著名幽灵船『玛丽·赛勒斯特号』。"}, {"id": 12, "image": "stands/黑檀木恶魔.png", "name": "黑檀木恶魔", "detail": "主体黑色，头部生有5只眼睛，其中一只位于额头部位，头顶生有一对末端为圆球的触角，胸口有两面盾牌状的装饰物，右手持一把匕首，但并不确定其能否使用匕首发动攻击。\r\n\r\n黑檀木恶魔的替身形象是基于“brunzeddos”，一种始于撒丁岛的古老青铜雕像，brunzeddos的战士雕像有四条手臂，一个长着四只眼睛和两个长角的异形头。身披盔甲，挥舞着两个盾牌，手里拿着一把短剑及一把弓。\r\n\r\n它所附身的娃娃类似非洲萨满(巫毒娃娃)，外表独特而原始。第三部连载的前一年适逢电影《灵异入侵》的上映，荒木也从电影中得到替身的创作灵感。\r\n\r\n替身名出自塔罗牌大阿卡娜牌的15号「恶魔（The Devil）」，象征混乱和不幸。"}, {"id": 13, "image": "stands/黄色节制.png", "name": "黄色节制", "detail": "黄色节制的外形是一团黄色软泥状，类似史莱姆的生物，但是没有五官也没有自我意识，它凭依在拉巴索身上，所以一般人也能看见。\r\n替身名出自塔罗牌大阿卡娜牌的14号「节制（Temperance）」，代表对立与和谐的融合。这方面反映出替身对物理伤害、热量和寒冷的不渗透性，讽刺的是，借由贪婪吞食一切生物来成长的能力与这张牌所代表的理念『节制』完全相反。\r\n\r\n黄色节制的能力灵感来自1958年的科幻恐怖电影《幽浮魔点》。"}, {"id": 14, "image": "stands/倒吊男.png", "name": "倒吊男", "detail": "倒吊男的外观是一具干枯、身体覆盖着绷带的男性尸体，但暴露在外的半颗大脑看起来像机械，同时，它与J·盖尔一样有两只右手，并以手腕制造出的攻击用的袖剑暗杀目标。它通常在地面上以反射的方式爬行，而不是典型的行走。\r\n替身名出自塔罗牌大阿卡纳牌的12号『倒吊人（The Hanged Man）』。代表美德和智慧，然而逆位方面却代表自私和欲望，完全反映了J·盖尔无耻的性格。\r\n\r\n倒吊男的形象是基于男性木乃伊，并且追加了机械元素，为的是消除一些看起来像是生物的感觉。"}, {"id": 15, "image": "stands/皇帝.png", "name": "皇帝", "detail": "皇帝是左轮手枪外型的替身，主体银色。在OVA中则把它描绘成金色。\r\n\r\n它的形象是来自左轮手枪和自动手枪的结合体。\r\n\r\n替身名出自塔罗牌大阿卡娜牌的4号「皇帝（The Emperor）」某四学家感到恐惧，象征秩序和控制。\r\n\r\n荒木表示，让枪立刻出现在使用者手中而不是从枪套里拔出来，是一种新奇的想法。"}, {"id": 16, "image": "stands/女帝.png", "name": "女帝", "detail": "主体红色，外观看起来像一个没有头发的壮硕女性，头上有两个头巾和一条披肩。\r\n\r\n女帝独特的口癖是「啾咪咪」\r\n\r\n替身名出自塔罗牌大阿卡娜牌的3号「女帝（The Empress）」，象征着新生命的成长，也暗示这个替身的性质。\r\n\r\n替身外型来自电影《人面疮系列》以及佛像。剧情中成长到一定程度后有模仿过李小龙的格斗动作和战吼，但使用连拳时的动作似乎参考了同样以李小龙为原型的拳四郎的招牌技能北斗百裂拳。"}, {"id": 17, "image": "stands/命运车轮.png", "name": "命运车轮", "detail": "命运之轮与『力』一样是凭附在物体上的替身，力绑定在船，命运之轮绑定在汽车，因此普通人同样也可以看到。替身为适应地形而改变汽车的特性，因此，在战斗时其外观会发生巨大变化。与命运之轮相连的普通客车将变成怪兽般的战车。\r\n\r\n在漫画官方上色版中，命运之轮被描绘成灰色，而动画则将其描绘成带有明亮金属零件的红色。\r\n\r\n替身名则出自出自塔罗牌大阿卡娜牌的10号「命运之轮（Wheel of Fortune）」，象征着变化和不可预测性。\r\n\r\n荒木表示，之所以设计汽车外型的替身是为了让故事看上去激烈有趣。替身的造型是取材六七十年代美国车以及1983年的美国恐怖电影《克丽丝汀魅力》。值得一提的是，与本体ZZ的同名的美国摇滚歌手ZZ Top他们于1983年发行的专辑《Eliminator》封面上有一台车和命运之轮外观十分相似。推测替身原型可能也是出自这里。"}, {"id": 18, "image": "stands/正义.png", "name": "正义", "detail": "原作漫画主体紫色，OVA版和动画版为白色，正义会在敌人面前伪装成一团巨大的雾，大到足以包围一个小墓地。当它被具体化时，看起来像戴着一顶宝石大皇冠的骷髅幽灵，头上和手上都像长袍一样雾气缭绕，像极了一个权力很大的王者。在动画中，正义和恩雅婆婆及J·盖尔一样都有两只右手。\r\n替身名出自塔罗牌大阿卡娜牌的11号「正义（The Justice）」。通常象征着公平的真理，然而对恩雅婆婆来说却极为讽刺，这个替身代表了她想报复世界的执着，特别是那些轻视她和她儿子的人。"}, {"id": 19, "image": "stands/恋人.png", "name": "恋人", "detail": "替身名出自塔罗牌大阿卡娜牌的6号「恋人（The Lovers）」，象征着人与人之间的牵绊，同时也象征着恋人使被寄生者与本体共享伤害的能力。\r\n\r\n恋人的外形同时结合了苍蝇与跳蚤的特征，并且还参考了科幻电影《霹雳五号（Short Circuit）》中的主角5号机器人。\r\n\r\n银色战车与法皇之绿进到乔瑟夫脑内和恋人战斗的剧情可能是参考了1987年美国科幻喜剧电影《惊异大奇航（Innerspace）》。而这部电影的灵感来自1966年的科幻电影《奇幻旅程（Fantastic Voyage）》。"}, {"id": 20, "image": "stands/太阳.png", "name": "太阳", "detail": "外表与真正的太阳完全一致，但是相比真正的太阳，与地表的距离更加接近，体积也更小。\r\n替身名出自塔罗牌大阿卡娜牌的19号「太阳（The Sun）」，象征着热情、光明和希望，但是这个替身的能力明显只继承了热和光……\r\n\r\n荒木飞吕彦表示相比其他设计精妙的塔罗牌替身，太阳的设计明显有些过于直白，但却也让太阳成为了设计上最贴合主题的塔罗牌替身。\r\n\r\n太阳在夜晚使天空如白昼般闪耀的情节可能取材自1959年美国科幻恐怖题材电视剧《迷离时空（The Twilight Zone）》的第75集《The Midnight Sun》。"}, {"id": 21, "image": "stands/死神13.png", "name": "死神13", "detail": "死神13的外貌如同真正的死神一般，有着小丑面具般的脸部小丑竟是我自己，头戴紫色高帽，身披深紫色披风，穿着金色的胸甲和臂铠，手持一把巨大的死神镰刀。替身可以漂浮在空中，斗篷之下藏着的身体并不具备下半身，可以说组成死神13主体的只有上半身以及镰刀。\r\n替身名的前半段出自塔罗牌大阿卡娜牌的13号「死神（Death）」，在塔罗牌中象征着“死亡与变革”，有着从毁灭中寻求希望的含义；替身名的后半段Thirteen为「死神（Death）」在塔罗牌大阿卡娜牌中的编号，与此同时13在许多地区都被视为不详之数。\r\n\r\n同时，死神13也是塔罗牌替身中唯一一个形象与荒木飞吕彦绘制的塔罗牌形象完全一致的替身。\r\n\r\n死神13的角色原型可能是电影《半夜鬼上床》的主角Freddy Krueger，两者的相同点在于都可以进入到梦中杀人。"}, {"id": 22, "image": "stands/审判.png", "name": "审判", "detail": "审判是一个大机器人外型的替身。戴着巨大的护肩，手上有三个金属光泽的手指。一个圆形头盔形状的零件位于其头部左侧上方。\r\n替身名出自塔罗牌大阿卡娜牌的20号「审判（Judgement）」，象征正确的判断，重生与赦免，或许这对应了该替身复活死者（虽然是虚假的）的能力。然而，替身的性质似乎更符合卡片的逆位意思：制造怀疑，欺骗人们做出错误的决定。\r\n\r\n审判的设计灵感来自日本超级英雄动画《人造人基凯达》以及石之森章太郎的《假面骑士系列》。审判实现愿望的能力受到阿拉丁神灯的经典故事的启发。"}, {"id": 23, "image": "stands/女教皇.png", "name": "女教皇", "detail": "女教皇的外型是披着一团看起来像蓑衣的皮毛，戴着部落面具的生物，眼神凶恶，牙齿外露，皮毛呈现液态金属状，上面长了一对肌肉发达的手臂。女教皇主体在漫画官方上色版中是红棕色及蓝色的皮毛。动画中是骨白色及棕色的皮毛。\r\n\r\n替身名出自塔罗牌大阿卡娜牌的2号「女教皇（The High Priestess）」，象征着直觉，神秘和神秘的知识。\r\n\r\n它的形象结合非洲巫术传统服装的元素。荒木表示，本体虽然是女性，但替身却画成了充满意外、不太像女人的样子。"}, {"id": 24, "image": "stands/盖布神.png", "name": "盖布神", "detail": "主体蓝色，外型是一小滩不成形的水小到可以藏在水壶里，一旦进入战斗状态水就会变化成类似兽爪的手。OVA中的盖布神则是多长着一张嘴。\r\n\r\n代表埃及九荣神中的大地之神盖布。\r\n\r\n荒木表示，盖布神是他在看过有人被洗脸盆的水淹死的事件后所受到的启发，认为有人在沙漠里淹死的故事应该会很有趣。"}, {"id": 25, "image": "stands/克努姆神.png", "name": "克努姆神", "detail": "代表埃及九荣神中的创造之神库努牡，暗指欧因哥有能力以任何他认为合适的方式重塑自己的身体。\r\n\r\n荒木表示，考虑了像水和火这样的各种形态的攻击，所以，画到这里的时候就想，该出现一些有悬念和干扰故事进展的替身能力了。"}, {"id": 26, "image": "stands/托托神.png", "name": "托托神", "detail": "外型是一本封面写着『Oingo Boingo Brothers Adventures』的连环漫画书，所以一般人也可以看见。替身没有任何攻击力，但受到的伤害也不会反馈到本体身上\r\n\r\n代表埃及九荣神中的书本之神托特。\r\n\r\n托托神的灵感出自古埃及的《托特书》(Book of Thoth)，这本书据说是托特写的，内容包含如何与动物交谈的知识，以及托特将自己视为神的能力，或具有超自然能力的能力。\r\n\r\n荒木表示，当时想如果敌人有预知能力的话，故事好像会变得好玩起来，而创作第三部的时期荒木也刚好遇到自己新婚，所以画这个替身时也不自觉一直在考虑着将来的事。"}, {"id": 27, "image": "stands/阿努比斯.png", "name": "阿努比斯本体", "detail": "替身最主要的能力是控制使用者。理论上阿努比斯可以控制任何接触到其刀身的人或动物，但其起初似乎会自主地“选择”使用者，只有被选中的人才能将其从刀鞘中拔出并被控制。\r\n\r\n被控制的人能立刻变成剑术高手。根据使用者本身对刀的了解不同会变成不同类型的剑术高手\r\n\r\n阿努比斯的替身与本体为同一物体，是一把纤细的长刀，代表埃及九荣神中的冥府之神阿努比斯。在阿努比斯说话时可以显示出一个胡狼人的虚像（类似埃及神话里阿努比斯的形象），但替身就是妖刀本身，这个虚像不能攻击人。\r\n\r\n荒木表示，画埃及九荣神的时候，比起设计造型，故事的精彩度和诡计的构思变得更为重要，例如被敌人蒙蔽内心或是被操控。"}, {"id": 28, "image": "stands/巴斯特女神.png", "name": "巴斯特女神", "detail": "外型是一个美式插座。在游戏《未来遗产》中，当玛莱雅阻挡或执行某些动作时，替身会有个猫头人身的虚像，类似于阿努比斯神那样。\r\n代表埃及九荣神中的埃及女神芭丝特。\r\n\r\n这个替身能力的灵感来自荒木的短篇集《变人偏屈列传》(故事以发明家尼古拉·特斯拉为原案来设计)，荒木也提到那时对电磁类非常感兴趣。"}, {"id": 29, "image": "stands/赛特神.png", "name": "赛特神", "detail": "本身并无实体，依附在阿雷西的影子上，替身能力发动时阿雷西的影子会变化为赛特神的真正形态。\r\n\r\n由于本身依附在阿雷西的影子上，因此外表上完全反映阿雷西本身的衣着、动作和手持物，不同之处在于头部会变成头顶带有独角的椭圆形，长着一对有金色眼眶的巨大双眼。漫画版中，赛特神的整体色调为暗紫色，动画版则为纯黑色。\r\n\r\n代表埃及九荣神中的风暴之神赛特。\r\n\r\n赛特神的外形是基于《铁人28号》以及纳斯卡线条设计的。"}, {"id": 30, "image": "stands/欧西里斯神.png", "name": "欧西里斯神", "detail": "是个肌肉发达的人型替身，手臂布满血管纹，手指上有吸盘。下半身被薄雾遮盖住，它的嘴和眼睛很小且没有眉毛。\r\n代表埃及九荣神中的冥神欧西里斯。\r\n\r\n这个替身的灵感来自荒木早期的短篇《武装扑克》，荒木表示，他为了替身的视觉效果而加入了非洲民族元素。"}, {"id": 31, "image": "stands/荷鲁斯神.png", "name": "荷鲁斯神", "detail": "外观看起来像一个巨大的，带着翼龙头的骷髅生物。有多个四肢，末端是鸟一样的爪子，躯干上有六只小胳膊，下半身有几对腿，这使它有点像蜘蛛或蜈蚣，除了战斗，荷鲁斯神很少展现它的真实样貌。\r\n\r\n代表埃及九荣神中的天空之神荷鲁斯。\r\n\r\n荷鲁斯神的替身形象取自翼龙的骨骸。"}, {"id": 32, "image": "stands/阿图姆神.png", "name": "阿图姆神", "detail": "外型如同一台相貌与小达比相似的银色机器人，金色的双眼下方写着小达比名字的首字母缩写TD，全身都有爱心装饰。\r\n\r\n代表埃及九荣神中的太阳神阿图姆。\r\n\r\n荒木表示，从小达比篇开始，他便喜欢在替身的造型里加入心形符号，这跟后来东方仗助的替身也充满了联系。\r\n\r\n替身的造型取自军用机器人。既像人又像人偶，所以有一种暧昧的魅力。"}, {"id": 33, "image": "stands/高音萨克斯.png", "name": "高音萨克斯", "detail": "替身名来自同名乐器。\r\n\r\n是第三部中除了亚空瘴气外少有的不以塔罗牌和埃及九荣神之名命名的替身，同时也是JOJO旧世界观唯一一个以乐器而非塔罗牌、九荣神、音乐作品、音乐人或音乐组织命名的替身。\r\n\r\n荒木表示创作这么一个替身以及与之战斗的情节是想在紧张的剧情中缓和一下气氛。"}, {"id": 34, "image": "stands/亚空瘴气.png", "name": "亚空瘴气", "detail": "主体白色，外观看起来像一个非常高大，头骨脸的人形怪物，戴着深蓝色类似刽子手头巾的面罩。面具上还可以看到两个突出的角，身上有心形图案。\r\n\r\n替身名取自英国摇滚乐团Cream band。\r\n\r\n它的形象是基于死神与法国中世纪刽子手，剧中也真的将瓦尼拉·艾斯给斩首。荒木表示，亚空瘴气自己把自己吞进去的设定是参考了无和无限的概念。\r\n\r\n由于亚空瘴气是第三部中唯二不以塔罗牌或埃及九荣神为原型的替身之一（另一个为高音萨克斯），在官方网站上，它的塔罗牌用一张空白的灰色卡片表示，中间有一个黑色的圆球，描绘了亚空瘴气的虚空形态。"}, {"id": 35, "image": "stands/杀手皇后.png", "name": "杀手皇后", "detail": "杀手皇后主体为浅粉色，外型为拳击手装扮的壮硕人形替身。\r\n它的冠是平的，而两个尖锐的三角形状像猫的耳朵一样立在头顶两侧。有着猫般垂直瞳孔。戴着黑色的镶有饰钉的皮革长手套，在食指的中指骨上有一个红色的小按钮，与传统雷管上的按钮相似。胃后面的空间是中空的，适合存放物体，造型参考了猫和骷髅。\r\n\r\n枯萎穿心攻击，外表是一个镀金球体（彩漫为绿色，动画为蓝色），顶部有一个表面可拆卸的旋钮，球体两侧都有坦克踏板，看起来像玩具坦克车。脸部是杀手皇后的猫头骨造型，前额上还加了一把向下的匕首。\r\n杀手皇后的三个形态名字全部来自于皇后乐队的歌曲。替身名「杀手皇后」来自《Killer Queen》，第二炸弹出自1974年的专辑《Sheer Heart Attack》[4]，阵亡形态（Bite the Dust）出自The Game中的歌曲《Another One Bites the Dust》[5]。\r\n\r\n值得一提的是，吉良吉影本人的人设来自于大卫·鲍伊，而大卫·鲍伊与皇后乐队唯一一首合作的歌曲叫做《Under Pressure》（压力之下），十分契合吉良的人设。"}]}
    provide('chinfo', chinfo)
    provide('stdinfo', stdinfo)
    provide('piclayout', picklayout)
    provide('picstyle', pickstyle)
    let [ins1,ins2,ins3,insf,inssf,w] = [store.state.ins1, store.state.ins2, store.state.ins3, store.state.insf, store.state.inssf, store.state.w]
    return {
      ins1,ins2,ins3,insf,inssf,w
    }
  }
}
</script>

<style>

  /* 基础设置 */
body,h1,h2,h3,h4,h5,h6,hr,p,blockquote,dl,dt,dd,ul,ol,li,pre,form,fieldset,legend,button,input,textarea,th,td{
    margin: 0;
    padding: 0;
}
ul,ol {
    list-style: none;
}
img {
  display: block;
	border:0;
}

b,strong {
    font-weight: 400;
}

h1,h2,h3,h4,h5,h6 {
    /* 父元素字号的百分比 */
    font-size: 100%;
    font-weight: normal;
}

i,em {
    /* 不倾斜 */
    font-style: normal;
}

u,ins,s,del {
    /* 去掉中划线和下划线 */
    text-decoration: none;
}

table {
    border: 1px solid #999;
    /* 相当于是cellspacing */
    border-spacing: 0;
    /* 1px边框 */
    border-collapse: collapse;
}

td,th {
    border: 1px solid #999;
}

input,button {
    /* 去掉轮廓线 */
    outline: none;
    border:none;
}

/* 风格设置 */
body {
    font: 12px/1.5 "Microsoft YaHei", Tahoma, Helvetica, Arial, "\5b8b\4f53", sans-serif;
    color: #333;
    background-color: rgb(207, 202, 197);

}

a {
    text-decoration: none;
    color: wheat;
}

a:hover {
    color:#DD302D;
