<script>
import {useStore} from "vuex";
import {computed, getCurrentInstance, ref, onMounted, inject} from 'vue'
import {disableScroll, enableScroll} from "no-scrolling";

export default {
  name: 'header_j',
  setup() {
    const store = useStore()
    let w = computed(()=>store.state.w)
    let piclayout = inject('piclayout')
    const instance = getCurrentInstance()
    let scrolltop = ref(window.scrollY || document.documentElement.scrollTop || document.body.scrollTop)
    // function forbidmove(ev){
    //   ev.preventDefault();
    // }
    function settingsout(){

      const el = instance.refs.settings
      const mask = instance.refs.mask
      // mask.addEventListener('touchmove',forbidmove)
      disableScroll()
      if (el.style.left === '-40vw'){
        el.style.top = window.scrollY+63+'px'
        setTimeout(()=>el.style.left = 0,50)
        setTimeout(()=>
        mask.style.display = 'block', 400)
        }
      else {
        el.style.left = -40+'vw'
      }
    }
    function sbarshow(){
      const el = instance.refs.sbar
      setTimeout(()=>{
        el.style.opacity = 1
        el.focus()
      })
    }
    function sbarhide(){
      const el = instance.refs.sbar
      setTimeout(()=>{
        el.style.opacity = 0
      },1)
    }
    function hidemask(){
      let mask = instance.refs.mask
      let el = instance.refs.settings
      mask.style.display = 'none'
      el.style.left = -40+'vw'
      // mask.removeEventListener('touchmove', forbidmove)
      enableScroll()
    }
    let ins1 = computed(() => {
      return store.state.ins1
    })
    let ins2 = computed(() => {
      return store.state.ins2
    })
    let ins3 = computed(() => {
      return store.state.ins3
    })
    onMounted(()=>{
      piclayout([1,1,1,1,0],[1,0,0,1,1])
    })
    let header1 = computed(()=>{
      if (w.value){return {'header1-slim': true}}
      else {return {'header1-slim': false,}}
    })
    let hleft1 = computed(()=>{
      if (w.value){return {'h-left-slim': true}}
      else {return {'h-left-slim': false,}}
    })
    let himg = computed(()=>{
      if (w.value){return {'h-img-slim': true}}
      else {return {'h-img-slim': false,}}
    })
    let btnctslim = computed(()=>{
      if (w.value){return {'btn-ct-slim': true}}
      else {return {'btn-ct-slim': false,}}
    })
    let hornslim = computed(()=>{
      if (w.value){return {'horn-slim': true}}
      else {return {'horn-slim': false,}}
    })
    let hmidslim = computed(()=>{
      if (w.value){return {'h-mid-slim': true}}
      else {return {'h-mid-slim': false,}}
    })
    let navslim = computed(()=>{
      if (w.value){return {'nav-slim': true}}
      else {return {'nav-slim': false,}}
    })
    let navbslim = computed(()=>{
      if (w.value){return {'nav-b-slim': true}}
      else {return {'nav-b-slim': false,}}
    })
    return {
      message_num: 0,
      ins1, ins2, ins3, w,
      settingsout,sbarshow,sbarhide,scrolltop,hidemask,
      header1, hleft1, himg, btnctslim, hornslim, hmidslim, navslim, navbslim
    }
  }
}


</script>

<template>

  <div v-show="ins1" class="header1 cf" :class="header1">

    <router-link to="/index" active-class="active">
      <div class="h-left fl" :class="hleft1">
        <img class="h-img" :class="himg" src="../assets/mypic/pesudo.png" alt="">
        <img class="h-img" :class="himg" src="../assets/mypic/home.png" alt="">
      </div>
    </router-link>
<!--    竖版仅有的搜索框和设置按钮-->
    <div v-show="w" class="slim-search">
      <div class="bigmask" ref="mask" @click="hidemask"></div>
      <img @click="settingsout" class="slim-top-img fr" src="../assets/slim/设置三道杠.png" alt="">
      <img @click="sbarshow" class="slim-top-img fr" src="../assets/slim/放大镜黑色.png" alt="">
      <label class="slim-label fr"><input v-on:mouseover="sbarshow" v-on:mouseleave="sbarhide" @blur="sbarhide" ref="sbar" type="text" placeholder="not JoJoのfans 站"></label>
    </div>
<!--    竖版仅有head部分结束-->

    <div class="btn-ct fr" :class="btnctslim">
      <div class="h_btn h_btn1"><a>登录</a></div>
      <div class="h_btn h_btn2"><a>注册</a></div>
    </div>

    <div v-show="message_num" class="horn fr" :class="hornslim">
      <img style="display: inline-block" src="../assets/images/laba.png" alt="">
      <span class="msg-num">&nbsp;{{ message_num }}</span>
    </div>

  </div>


  <div v-show="ins2" class="h-mid cf" :class="hmidslim">
    <div class="search fr"><img style="margin-top: 3px" src="../assets/mypic/serch_icon.png" alt=""></div>
    <label class="hmid-label fr"><input type="text" placeholder="not JoJoのfans 站"></label>
  </div>

  <div v-show="ins3" class="nav" :class="navslim" ref="settings">
    <!--    <div class="nav-b1 ib nav-b">首页</div>-->
    <router-link to="/index" active-class="active">
      <div class="nav-b1 ib nav-b" :class="navbslim">首&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;页</div>
    </router-link>
    <router-link to="/gmall" active-class="active">
      <div class="nav-b2 ib nav-b" :class="navbslim">静态预览</div>
    </router-link>

    <router-link to="/aboutme" active-class="active">
      <div class="nav-b3 ib nav-b" :class="navbslim">关于我</div>
    </router-link>
    <router-link to="/picset" active-class="active">
      <div class="nav-b4 ib nav-b" :class="navbslim">美女图集</div>
    </router-link>
    <!--    <div class="nav-b5 ib nav-b">人物概览</div>-->
    <router-link to="/characters/list/1" active-class="active">
      <div class="nav-b5 ib nav-b" :class="navbslim">人物概览</div>
    </router-link>

    <router-link to="/seasons" active-class="active">
      <div class="nav-b6 ib nav-b" :class="navbslim">分季了解</div>
    </router-link>

    <br v-show="w"><br v-show="w"><br v-show="w"><br v-show="w">

    <hr v-show="w" class="slim-lefthr">

    <div v-show="w" class="slim-leftbot"></div>

  </div>

</template>

