@media screen and (min-width: 320px) and (max-width: 700px) {
  .header1-slim {
    width: 100%;
    height: 63px;
    background-color: rgba(255, 255, 255, 1);
    visibility: visible;
    position: sticky;
    top: 0;
    z-index: 9;
  }

  .h-left-slim {
    height: 7vh;
    position: absolute;
    z-index: -0;
    background-color: #ffffff;
    opacity: 0.8;
  //margin-top: 7px;
  }

  .h-img-slim {
    height: 80%;
  }
  .slim-search{
    //border: 1px black solid;
    width: 55vh;
    height: 70%;
    float: right;
    margin-top: 5px;
    margin-right: 10px;
    transition-duration: 0.4s;
  }
  .slim-label{
    //border: 1px black solid;
    width:30vh;
    float: left;
    margin-left: 10vh;
  }
  input{
    float: left;
    height: 5vh;
    width: 35vh;
    margin-right: 20vh;
