<script>
import {useStore} from "vuex";
import {computed} from 'vue'
export default {
  name: "footer_v",
  components:{},
  setup(){
    const store = useStore()
    let insf = computed(()=>{return store.state.insf})
    return {
      insf,
    }
  }
}
</script>

<template>
<footer v-if="insf">你好图片加载若很慢等会儿就好</footer>
</template>

<style scoped>
footer{
  width: 1000px;
  height: 110px;
  margin: 0 auto;
  background-color: #8a8a8a;
  font-size: 60px
}
@media screen and (min-width: 320px) and (max-width: 700px){
  footer{
    width: 100%;
    height: 20vw;
    font-size: 22px;
  }
}
</style>