import {createStore} from 'vuex'


const store = createStore({
    state: {
        ins1: 1,
        ins2: 1,
        ins3: 1,
        insf: 1,
        inssf: 1,
        w: 1,
        h: 2,
        m: 59,
        s: 59,
    },
    mutations: {
        CHEAD(state, value) {
            state.ins1 = value[0]
            state.ins2 = value[1]
            state.ins3 = value[2]
            state.insf = value[3]
            state.inssf = value[4]
        },
        CW(state,value) {
            console.log('收到')
            state.w = value
        }
    },
    actions: {},
    modules: {},
    getters: {
        h(state){
            setTimeout(()=>{
                state.h--
            }, 3600000)
            return state.h
        },
        m(state){
            setTimeout(()=>{
                state.m--
                if(state.m<0){state.s=59}
            }, 60000)

            return state.m
        },
        s(state){
            setTimeout(()=>{
                state.s--
                if(state.s<0){state.s=59}
            }, 1000)

            return state.s
        }
    }
})
export default store