<script>
import {onMounted, inject, getCurrentInstance, computed} from "vue";

import {useStore} from "vuex";

export default {
  name: "seasons_j",
  setup(){
    let piclayout = inject('piclayout')
    const store = useStore()
    let w = computed(()=>store.state.w)
    console.log(w.value)
    function cvdraw(arr,c){
      let cv = getCurrentInstance()
      let fillimg = new Image()
      fillimg.src = require('@/assets/slim/碎玻璃小.png')
      cv = cv.refs[c]
      let cv1 = cv.getContext('2d')
      cv1.fillStyle = cv1.createPattern(fillimg, 'repeat-x')
      cv1.globalAlpha = 0.8
      // cv1.fillStyle = 'rgba(0,0,0,0.9)'
      cv1.lineWidth = 2
      cv1.beginPath()
      cv1.moveTo(arr[0][0],arr[0][1])
      for(let i=1;i<arr.length;i++){
        cv1.lineTo(arr[i][0],arr[i][1])
      }
      cv1.closePath()
      cv1.fill()

    }
    onMounted(()=>{

      piclayout([0,0,0,0,0], [1,0,1,0,1])
      cvdraw([[0,9],[111,4],[155,35],[172,110]],'c1')
      cvdraw([[24,19],[246,110],[150,127],[62,179]], 'c2')
      cvdraw([[7,1],[85,195],[6,236]], 'c3')
      cvdraw([[11,206],[105,36],[199,65],[180,105]], 'c4')
      cvdraw([[0,92],[181,11],[32,237]], 'c5')
      cvdraw([[6,121],[93,13],[96,230]], 'c6')
      cvdraw([[1,122],[169,4],[150,155],[100,125]], 'c7')
      cvdraw([[65,4],[210,82],[2,110]], 'c8')
    })

    return {
      w
    }
  }
}
</script>

