import { createRouter, createWebHashHistory} from "vue-router"
// import { createRouter, createWebHistory} from "vue-router"

import banner_h from "@/pages/banner.vue";
import jojo_charactor from "@/pages/jojocharactor.vue";
import g_mall from "@/pages/gmall.vue";
import chdetail_pages from "@/pages/chdetailpages.vue";
import ch_container from "@/pages/chcontainer.vue";
import pic_set from "@/pages/picset.vue";
import seasons_j from "@/pages/seasons.vue";
import about_me from "@/pages/aboutme.vue";
import news_no from "@/pages/news.vue";



const router = createRouter({
    history: createWebHashHistory(),
    routes:[
        {
            name: 'root',
            path:'/',
            component: banner_h,
            meta: {
                isAuth: false,
                title: '头部',
            }
        },
        {
            name: 'head_banner',
            path:'/index',
            component: banner_h,
            meta: {
                isAuth: false,
                title: '首页',
            }
        },
        {
            name: 'news',
            path:'/news/:id',
            component: news_no,
            meta: {
                isAuth: false,
                title: '新闻',
            }
        },
        {
            name: 'characters_container',
            path: '/characters',
            component: ch_container,
            meta: {
                isAuth: false,
                title: '人物区1',
            },
            children: [
                {
                    path: 'list/:id',
                    component: jojo_charactor
                },
                                {
                    path: 'detail/:id',
                    component: chdetail_pages
                },
            ]
        },
        {
            name: 'aboutme',
            path:'/aboutme',
            component: about_me,
            meta: {
                isAuth: false,
                title: '关于我',
            },
        },
        {
            name: 'gmall',
            path:'/gmall',
            component: g_mall,
            meta: {
                isAuth: false,
                title: '技术预览区',
            },
        },
        {
            name: 'picset',
            path:'/picset',
            component: pic_set,
            meta: {
                isAuth: false,
                title: '图集',
            },
        },
        {
            name: 'seasons',
            path:'/seasons',
            component: seasons_j,
            meta: {
                isAuth: false,
                title: '分季',
            },
        },
    ]
})

export default router